<template>
  <div class="top-3">
    <backButton :style="{position: 'relative', left: '1rem'}" @click="$router.go(-1)"/>
    <storyCard :warna="warna" :data="result"/>
  </div>
</template>

<script>
import storyCard from '../components/mid/storyCard.vue'
import backButton from '../components/mats/backBtn.vue'

  export default{
    name: 'singlePost',
    data() {
      return {
        result: {}
      }
    },
    components: {
      storyCard,
      backButton
    },
    props: {
      warna: Object
    },
    async created() {
        try {
          this.$emit('hilang-navbar', false)
        let results = await this.axios.get('https://jsonplaceholder.typicode.com/posts');
        results.data.forEach((d) => {
          if(d.id == this.$route.params.id) {
            this.result = d
          } 
        })
      } catch (err){
        alert(err)
      }
    }
  }
</script>

<style scoped>
.top-3 {
  position: relative;
  top: 1rem;
}
</style>
